 en caso de mora. Transcurrido el plazo, si el deudor no paga, el mutuante podrá iniciar un proceso ejecutivo, porque tiene un título valor que acredita la obligación.")]),_c('div',{staticClass:"bg-color-8 p-4",attrs:{"data-aos":"fade-left"}},[_c('div',{staticClass:"row align-items-start"},[_c('div',{staticClass:"col-lg-auto"},[_c('img',{staticClass:"mx-auto",staticStyle:{"max-width":"90px"},attrs:{"src":require("@/assets/curso/tema7/2.svg")}})]),_c('div',{staticClass:"col-lg"},[_c('p',{staticClass:"mb-0"},[_vm._v("La deuda incluye el capital, los intereses pactados y, si hay lugar, los moratorios, conforme a la Ley. Esta situación no solo refleja la utilidad del mutuo, sino también los riesgos que asume quien presta sin garantías suficientes o sin respaldo documental adecuado.")])])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"bg-color-11 mb-5",attrs:{"data-aos":"fade-up"}},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-auto mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema7/4.png"),"alt":""}})])]),_c('div',{staticClass:"col-lg"},[_c('div',{staticClass:"p-4"},[_c('p',{staticClass:"mb-0",attrs:{"data-aos":"fade-up"}},[_vm._v("En la práctica, el contrato de mutuo es ampliamente utilizado en contextos familiares, sociales, empresariales y financieros. Lo celebran padres con hijos, amigos entre sí, pequeños empresarios, microfinancieras, cooperativas y entidades bancarias. También puede funcionar como instrumento accesorio dentro de contratos más complejos, como los de construcción o compraventa, donde se anticipa una suma que se restituye después. En todos los casos, su eficacia y validez dependen de una correcta redacción, del cumplimiento de las formalidades mínimas y, sobre todo, del equilibrio entre las prestaciones.")])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;retur